@keyframes hand-rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stop-watch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stop-watch__hand {
  transform-origin: bottom;
  animation: linear infinite hand-rotate;
  position: absolute;
}

.stop-watch__hand--secund {
  background-color: green;
  width: 1vmin;
  height: 40vmin;
  animation-duration: 5s;
  bottom: 40vmin;
}

.stop-watch__hand--minute {
  background-color: #00f;
  width: 2vmin;
  height: 20vmin;
  animation-duration: 60s;
  bottom: 40vmin;
}

.stop-watch__mid-dot {
  z-index: 1;
  background-color: #ffbf00;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
}

